import React, {Component} from 'react';
import MainContainer from './MainContainer';
import ProjectsPanel from './ProjectsPanel';
import TeamsPanel from './TeamsPanel';
import EmployeesPanel from './EmployeesPanel';

class Overview extends Component{
    render() {
        return(
            <MainContainer navbar="WEB422 - Project Portal" sidebar="Overview">
              <h1 className="page-header">Overview</h1>
              <div className="row">
                <div className="col-md-4">
                  <ProjectsPanel title="Projects" dataSource="https://mighty-wildwood-35586.herokuapp.com/projects" />
                </div>
                <div className="col-md-4">
                  <TeamsPanel title="Teams" dataSource="https://mighty-wildwood-35586.herokuapp.com/teams" />
                </div>
                <div className="col-md-4">
                  <EmployeesPanel title="Employees" dataSource="https://mighty-wildwood-35586.herokuapp.com/employees" />
                </div>
              </div>
        </MainContainer>
        )
    }
}

export default Overview;