import React, { Component } from 'react';
import moment from 'moment';
import MainContainer from './MainContainer';

class Employees extends Component{
    constructor(props){
        super(props);
        this.state = {
            employees : []
        }
    }

    componentDidMount() {
        fetch(this.props.dataSource)
        .then(res => res.json())
        .then(data => {
            this.setState({employees : data});
        }).catch(err => {
            console.log("error");
        })
    }

    render(){
        const employees = this.state.employees;

        function compare(a,b){
            if(a.FirstName < b.FirstName){
                return -1;
            }
            if(a.FirstName > b.FirstName){
                return 1;
            }
            return 0;
        }

        employees.sort(compare);

        return(
            <MainContainer navbar="WEB422 - Project Portal" sidebar="Employees">
                <h1 className="page-header">Employees</h1>
                <table className="table table-striped table-bordered">
                    <tbody>
                        <tr>
                            <th>Name &amp; Position</th>
                            <th>Address</th>
                            <th>Phone Num</th>
                            <th>Hire Date</th>
                            <th>Salary Bonus</th>
                        </tr>
                        {employees.map((employee) => 
                            <tr key={employee._id}>
                                <td>{employee.FirstName + ' ' + employee.LastName + ' - ' + employee.Position.PositionName}</td>
                                <td>{employee.AddressStreet + ', ' + employee.AddressCity + ' ' + employee.AddressState + ', ' + employee.AddressZip}</td>
                                <td>{employee.PhoneNum + ' ex: ' + employee.Extension}</td>
                                <td>{moment(employee.HireDate).format('LL')}</td>
                                <td>{'$ ' + employee.SalaryBonus}</td>
                            </tr>)}
                    </tbody>
                </table>
            </MainContainer>
        )
    }
}

export default Employees;