import React, { Component } from 'react';
import moment from 'moment';
import MainContainer from './MainContainer';

class Projects extends Component{
    constructor(props){
        super(props);
        this.state = {
            projects : []
        }
    }

    componentDidMount() {
        fetch(this.props.dataSource)
        .then(res => res.json())
        .then(data => {
            this.setState({projects : data});
        }).catch(err => {
            console.log("error");
        })
    }

    render(){
        const projects = this.state.projects;
        
        //get the Number value in the value of ProjectName of each team
        function getProjNum(project){
            let regex = /\d+/g;
            let found = project.ProjectName.match(regex).map(Number);
            return found[0];
        }

        //a compare function for sorting the teams by the Number value in the ProjectName
        function compare(a, b){
            return getProjNum(a) - getProjNum(b);
        }

        //sort the projects
        projects.sort(compare);

        return(
            <MainContainer navbar="WEB422 - Project Portal" sidebar="Projects">
                <h1 className="page-header">Projects</h1>
                <table className="table table-striped table-bordered">
                    <tbody>
                        <tr>
                            <th>Name</th>
                            <th>Description</th>
                            <th>Start Date</th>
                            <th>End Date</th>
                        </tr>
                        {projects.map((project) => 
                            <tr key={project._id}>
                                <td>{project.ProjectName}</td>
                                <td>{project.ProjectDescription}</td>
                                <td>{project.ProjectStartDate ? moment(project.ProjectStartDate).format('LL') : 'n/a'}</td>
                                <td>{project.ProjectEndDate ? moment(project.ProjectEndDate).format('LL') : 'n/a'}</td>
                            </tr>)}
                    </tbody>
                </table>
            </MainContainer>
        )
    }
}

export default Projects;