import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';

class ProjectsPanel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            projects: []
        }
    }

    componentDidMount() {
        fetch(this.props.dataSource)
        .then(res => res.json())
        .then(data => {
            this.setState({projects : data});
        }).catch(err => {
            console.log("error");
        })
    }

    render() {
        const projects = this.state.projects;
        
        //get the Number value in the value of ProjectName of each team
        function getProjNum(project){
            let regex = /\d+/g;
            let found = project.ProjectName.match(regex).map(Number);
            return found[0];
        }

        //a compare function for sorting the teams by the Number value in the ProjectName
        function compare(a, b){
            return getProjNum(a) - getProjNum(b);
        }

        //sort the projects
        projects.sort(compare);

        return (
            <div className="panel panel-default">
                    <div className="panel-heading">
                      <h3 className="panel-title">{this.props.title}</h3>
                    </div>
                    <div className="panel-body">
                      <div className="table-responsive overview-table">
                        <table className="table table-striped table-bordered">
                          <tbody>
                            {projects.map((project) =>
                                <tr key={project._id}>
                                    <td>{project.ProjectName}</td>
                                    <td>{'Active ' + moment().diff(moment(project.ProjectStartDate),'days') + ' days'}</td>
                                </tr>)}
                          </tbody>
                        </table>
                      </div>
                      <Link to="/projects" className="btn btn-primary form-control">View All Project Data</Link>
                    </div>
                  </div>
        )
    }
}

export default ProjectsPanel;