import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import Overview from './components/Overview';
import Projects from './components/Projects';
import Teams from './components/Teams';
import Employees from './components/Employees';
import NotFound from './components/NotFound';

class App extends Component {
  render() {
    return (
      <Switch>
        <Route exact path='/' render={() => (
          <Overview />
        )} />

        <Route exact path='/projects' render={() => (
          <Projects dataSource="https://mighty-wildwood-35586.herokuapp.com/projects"/>
        )} />

        <Route exact path='/teams' render={() => (
          <Teams dataSource="https://mighty-wildwood-35586.herokuapp.com/teams"/>
        )} />

        <Route exact path='/employees' render={() => (
          <Employees dataSource="https://mighty-wildwood-35586.herokuapp.com/employees"/>
        )} />

        <Route render={() => (
          <NotFound />
        )} />
      </Switch>
    );
  }
}

export default App;